.checkout-product{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}
.checkoutProduct-info{
    padding-left: 20px;

}
.checkoutProduct-info > button{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;

}
.checkoutProduct-image{
   object-fit: contain;
   width: 180px;
   height: 180px;
}
.checkoutProduct-rating{
    display:flex;
}
.checkoutProduct-title{
  font-size: 17px;
  font-weight: 800;
}